.client-billing {
    table {
        font-weight: bold;
        font-family: monospace;

        &.current-invoices td {
            vertical-align: middle;
            font-size: 16px;

            .badge-pill {
                font-size: 16px !important;
            }
        }

        .download-invoice {
            padding: 0;
        }

        &.account-history th {
            font-size: 16px;
        }
    }

    &__limit-div {
        white-space: pre-wrap;
    }
}